import React, {PureComponent} from "react";
import PropTypes from "prop-types";
import _ from "lodash";

import OraFloatingLabelWrapper from "./OraFloatingLabelWrapper";
import {OraLabel} from "../../../components/OraCommonComponents";

class SelectWithFloatingLabel extends PureComponent {
    static propTypes = {
        id: PropTypes.string.isRequired,
        options: PropTypes.arrayOf(
            PropTypes.shape({
                value: PropTypes.any.isRequired,
                label: PropTypes.string
            })
        ).isRequired,
        value: PropTypes.any,
        placeholder: PropTypes.string,
        autocomplete: PropTypes.bool,
        children: PropTypes.any,
        onChange: PropTypes.func,
        disabled: PropTypes.bool
    };

    renderOptions() {
        return this.props.options.map(selectOption => {
            return (
                <option key={selectOption.value} value={selectOption.value}>
                    {selectOption.label}
                </option>
            );
        });
    }

    render() {
        const {id, autocomplete, placeholder, children, onChange, value, disabled} = this.props;
        return (
            <OraFloatingLabelWrapper autoComplete={autocomplete} maximumWidth={true} className="o-floating-label o-select u-spacing-l">
                <select
                    id={id + "Select"}
                    name="select"
                    aria-describedby="floating-label__placeholder1"
                    className={"opl-select-primary opl-input--size-m" + (!_.isEmpty(this.props.options) ? " is-not-empty" : "")}
                    value={value || ""}
                    onChange={onChange}
                    disabled={disabled}
                >
                    {this.renderOptions()}
                </select>
                <span className="o-select__arrow" />
                <OraLabel
                    htmlFor={id}
                    className="label o-floating-label__placeholder o-floating-label__p-2"
                >
                    {placeholder}
                </OraLabel>
                {children}
            </OraFloatingLabelWrapper>
        );
    }
}

export default SelectWithFloatingLabel;